/* Please see documentation at https://docs.microsoft.com/aspnet/core/client-side/bundling-and-minification
for details on configuring this project to bundle and minify static web assets. */

a.navbar-brand {
    white-space: normal;
    text-align: center;
    word-break: break-all;
}

/* Sticky footer styles
-------------------------------------------------- */
html {
    font-size: 14px;
}

@media (min-width: 768px) {
    html {
        font-size: 16px;
    }
}

.mdb-color {
    background-color: #b5beec;
}

.border-top {
    border-top: 1px solid #e5e5e5;
}

.border-bottom {
    border-bottom: 1px solid #e5e5e5;
}

.box-shadow {
    box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05);
}

button.accept-policy {
    font-size: 1rem;
    line-height: inherit;
}

/* Sticky footer styles
-------------------------------------------------- */
html {
    position: relative;
    min-height: 100%;
}

body {
    /* Margin bottom by footer height */
    margin-bottom: 60px;
}

a {
    text-decoration: none;
}

header {
    margin-bottom: 75px;
}

nav {
    position: fixed !important;
    z-index: 99;
    top: 0;
    width: 100%;
}

.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    white-space: nowrap;
    /* Set the fixed height of the footer here */
    height: 60px;
    line-height: 60px; /* Vertically center the text there */
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

.disabled {
    pointer-events: none;
    cursor: default;
}

.card .card-body .card-title h4 {
    font-weight: bold;
}

.card .card-body .card-title .font-small {
    align-self: flex-end;
}

.card .card-body .quantity-buttons {
    display: flex;
    padding: 0;
    align-self: flex-end;
    justify-content: flex-end;
}

#food-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

    #food-container .card {
        margin: 0.25rem;
    }

        #food-container .card .card-body {
            padding-bottom: 0;
        }

            #food-container .card .card-body .card-title {
                display: flex;
                flex-direction: column;
                width: min-content;
            }

            #food-container .card .card-body .quantity-buttons li:nth-child(2) {
                flex: 0.23;
            }

        #food-container .card .card-footer {
            display: flex;
            justify-content: flex-end;
            padding: 0.25rem;
        }

            #food-container .card .card-footer ul {
                margin: 0;
            }

.delete-icon {
    width: 40px;
    height: 40px;
    border: 1px solid #000;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
}

.font-small {
    font-size: 0.5em;
}

#cart-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}

    #cart-container #summary-info {
        flex: 0.25;
    }


    #cart-container #order-container {
        flex: 0.65;
    }

#order-container {
    display: flex;
    flex-flow: column wrap;
}

    #order-container .card {
        margin-bottom: 10px;
        flex-direction: row;
    }

        #order-container .card .card-body {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }

            #order-container .card .card-body .quantity-buttons {
                width: 50%;
            }

            #order-container .card .card-body p {
                font-weight: bold;
            }

        #order-container .card .order-card-img {
            width: 50%;
        }

        #order-container .card .card-body .card-title {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }

            #order-container .card .card-body .card-title .order-card-header {
                display: flex;
                flex-direction: column;
                width: min-content;
            }

                #order-container .card .card-body .card-title .order-card-header * {
                    margin: 0;
                }

.quantity-input {
    text-align: center;
}

#total-price {
    text-align: center;
}

.order-bordered {
    border: 1px solid #dee2e6;
}

    .order-bordered .order-row {
        border-bottom: 1px solid #dee2e6;
    }

        .order-bordered .order-row:last-child {
            border-bottom: 0;
        }

    .order-bordered .order-cell {
        border-right: 1px solid #dee2e6;
    }

        .order-bordered .order-cell:last-child {
            border-right: 0;
        }

.order-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

    .order-row .order-cell {
        display: flex;
        flex-direction: column;
        justify-content: center;
        flex: 1;
    }

.order-header * {
    font-weight: bold;
}

.order-cell {
    padding: 0.5rem;
}